import { Heading } from '@chakra-ui/layout';
import { Divider } from '@chakra-ui/layout';
import { Box } from '@chakra-ui/layout';
import React from 'react';
import { useSelector } from 'react-redux';
import NeedToCombine from '../NeedToCombine';
import Statter from '../Statter';
import {
  selectA,
  selectNSum,
  selectP,
  selectSigma,
  selectEmpirichne,
  selectKritichne,
} from '../../features/inputs';

const ExerciseOneStats = () => {
  const nSum = useSelector(selectNSum);
  const a = useSelector(selectA);
  const sigma = useSelector(selectSigma);
  const p = useSelector(selectP);
  const empirichne = useSelector(selectEmpirichne);
  const kritichne = useSelector(selectKritichne);

  return (
    <Box
      borderWidth="1px"
      borderRadius="lg"
      boxShadow="lg"
      w="full"
      p="2"
      mb="10"
    >
      <Heading textAlign="center">–°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞</Heading>
      <Divider my="2" />
      <Box p="2">
        <Statter label="–°—É–º–∞ –≤–∏–±—ñ—Ä–∫–∏" value={nSum} />
        <Divider my="2" />
        <Statter label="a (–ú–∞—Ç–µ–º–∞—Ç–∏—á–Ω–µ —Å–ø–æ–¥—ñ–≤–∞–Ω–Ω—è)" value={a} />
        <Divider my="2" />
        <Statter label="&#963; (—Å—ñ–≥–º–∞ &#8730;D)" value={sigma} />
        <Divider my="2" />
        <Statter label="P (–¢–µ–æ—Ä–µ—Ç–∏—á–Ω–∞ –π–º–æ–≤—ñ—Ä–Ω—ñ—Å—Ç—å)" value={p} symbol="P" />
        <Divider my="2" />
        <NeedToCombine />
        <Divider my="2" />
        <Statter label="–ï–º–ø—ñ—Ä–∏—á–Ω–µ –∑–Ω–∞—á–µ–Ω–Ω—è —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏" value={empirichne} />
        <Divider my="2" />
        <Statter label="–ö—Ä–∏—Ç–∏—á–Ω–µ –∑–Ω–∞—á–µ–Ω–Ω—è —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏" value={kritichne} />
        <Divider my="2" />
        <Heading as="h2" size="lg">
          {empirichne > kritichne
            ? `–ù–æ—Ä–º–∞–ª—å–Ω–∏–π –∑–∞–∫–æ–Ω –≤—ñ–¥—Ö–∏–ª—è—î—Ç—å—Å—è ${empirichne} > ${kritichne}`
            : '–ù–æ—Ä–º–∞–ª—å–Ω–∏–π –∑–∞–∫–æ–Ω –ø—ñ–¥—Ç–≤–µ—Ä–¥–∂—É—î—Ç—å—Å—è'}
        </Heading>
      </Box>
    </Box>
  );
};

export default ExerciseOneStats;
